export default {
	props: {
		item: Object,
		voSocketPay: Boolean
	},
	data() {
		return {

			tableData: [],
			centerDialogVisible: false, //二维码弹窗
			fullscreenLoading: false,
			//order:'',
			myInterval: null, //订单状态轮询
			myIntervalPay: null, //支付结果轮询
			voSocket: false,

			isLunxun: false,
			codes: ''
		}
	},

	methods: {


		checke_isButtenz() {
			this.$emit("getordlist")
		},

		//支付
		payOrder(id) {
			if (this.fullscreenLoading) {
				uni.showModal({
					title: '提示',
					content: '订单生成中请稍后',
				});
				this.fullscreenLoading = false;
				return false;
			}
			let order = {
				id: id
			}
			this.fullscreenLoading = true;
			this.$http.get(this.$urlconfig.getnpayos, order, {}).then(data => {
				this.fullscreenLoading = false;
				if (data.status === 0) {

				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					});
				}
			});
		},

		//操作
		operationRow(scope, type) {
			if (this.fullscreenLoading) {
				uni.showModal({
					title: '提示',
					content: '上一个操作还没有响应，请稍后再试',
				});
				this.fullscreenLoading = false;
				return false;
			}
			this.fullscreenLoading = true;
			let order = {
				id: scope.voOrder.id,
				type: type
			};
			//操作
			this.$http.post(this.$urlconfig.getoscaozuo, order, {}).then(data => {
				this.fullscreenLoading = false;
				if (data.status === 0) {
					uni.showToast({
						title: '操作成功',
						icon: "none"
					});
					this.checke_isButtenz();
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					});
				}
			});

			//11--》3关单  或者 13 抢单确认-->12 支付按键-->支付成功变成4 （送货者操作4 变成16） --> 16 确认

			// -->状态==5  （去评价）   status==3|| 17   传给后端 11再次开启
		},

		//选择此家
		choice(props, scope, type) {
			if (this.fullscreenLoading) {
				uni.showToast({
					title: '操作中请稍后',
					icon: "none"
				});
			}
			this.fullscreenLoading = true;
			let order = {};
			order.id = props.voOrder.id;
			order.type = type;
			order.orderCommonOfferId = scope.orderCommonOffer.id;
			order.commodityZongJiage = scope.orderCommonOffer.commodityZongJiage;
			//查询有没有待支付订单
			this.$http.post(this.$urlconfig.getoscaozuo, order, {}).then(data => {
				this.fullscreenLoading = false;
				if (data.status === 0) {
					uni.showToast({
						title: '操作成功',
						icon: "none"
					});
					console.log('调用刷新')
					this.checke_isButtenz();
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					});
				}
			});
		},
	}
}
